html {
    color: #000;
    background: #fff;
    overflow-x: hidden;
}

body {
    font-family: 'microsoft yahei','Helvetica', 'SimHei', 'sans-serif';
    overflow-x: hidden;
    /*font-size: */
}

body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
textarea,
p,
blockquote,
th,
td {
    margin: 0;
    padding: 0
}

body,
html,
a,
img,
button,
input,
textarea,
li,
ul,
ol,
div,
i,
td,
th {
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}

table {
    border-collapse: collapse;
    border-spacing: 0
}

img {
    border: 0
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
    font-style: normal;
    font-weight: normal
}

ol,
ul {
    list-style: none
}

caption,
th {
    text-align: left
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: 100%;
    font-weight: normal
}

sup {
    vertical-align: text-top
}

sub {
    vertical-align: text-bottom
}

input,
textarea,
select {
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    *font-size: 100%;
    outline: none;
}

.autop {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.tc {
    text-align: center;
}

.tr {
    text-align: right;
}

.tl {
    text-align: left;
}

.num_en {
    font-family: 'Helvetica';
}

.weight {
    font-weight: bold;
}

._up {
    color: #f24957;
}

._down {
    color: #1fbf62;
}

._none {
    color: #aaa;
}

