.fund_loading {
    width: 60%;
    margin: 50% auto 0 auto;
    border: 1px solid #e2e4e6;
    border-radius: 1rem;
    background-color: #fff;
    height: 5rem;
    line-height: 5rem;
}

.fund_newloading {
    width: 17%;
    margin: 50% auto 0 auto;
    border: 1px solid #e2e4e6;
    border-radius: 1rem;
    background: no-repeat url('../../assets/img/load.gif');
    background-position: 50%;
    background-color: rgba(0, 0, 0, .8);
    height: 4rem;
    line-height: 4rem;
}

.fund_loadingmodal {
    position: absolute;
    z-index: 999;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0);
    top: 0;
}

.fund_loadingtext {
    text-align: center;
    width: 90%;
    margin: 0 auto;
    background: no-repeat url('../../assets/img/load.gif');
    background-position: left;
    background-size: auto 40%;
}

.fund_loadingnoimg {
    text-align: center;
    width: 90%;
    margin: 0 auto;
}

.fund_nodatamodal {
    position: absolute;
    z-index: 2;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0);
    top: 0;
}

.inline {
    display: inline-block;
}

.fr {
    float: right;
}

.fl {
    float: left;
}

.none {
    display: none;
}

.va_m {
    vertical-align: middle;
}


/**/

#pullDown,
#pullUp {
    background: #fff;
    padding: 5px 10px;
    color: #888;
    margin: 0 auto;
    line-height: 40px;
    font-weight: bold;
    font-size: .9rem;
}

#pullDown .pullDownIcon,
#pullUp .pullUpIcon {
    display: inline-block;
    float: left;
    background: url(../../../assets/img/pull-icon@2x.png) 0 0 no-repeat;
    -webkit-background-size: 40px 80px;
    background-size: 40px 80px;
    -webkit-transition-property: -webkit-transform;
    -webkit-transition-duration: 250ms;
    width: 40px;
    height: 40px;
}

.pullUpLabel,
.pullDownLabel {
    display: inline-block;
    height: 40px;
    width: auto;
}

#pullDown .pullDownIcon {
    -webkit-transform: rotate(0deg) translateZ(0);
}

#pullUp .pullUpIcon {
    -webkit-transform: rotate(-180deg) translateZ(0);
}

#pullDown.flip .pullDownIcon {
    -webkit-transform: rotate(-180deg) translateZ(0);
}

#pullUp.flip .pullUpIcon {
    -webkit-transform: rotate(0deg) translateZ(0);
}

#pullDown.loading .pullDownIcon,
#pullUp.loading .pullUpIcon {
    /*background-position: 0 100%;*/
    background: no-repeat url(../../../assets/img/lging.gif) center;
    -webkit-transform: rotate(0deg) translateZ(0);
    -webkit-transition-duration: 0ms;
    -webkit-animation-name: loading;
    -webkit-animation-duration: 2s;
    -webkit-animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
}

@-webkit-keyframes loading {
    from {
        -webkit-transform: rotate(0deg) translateZ(0);
    }
    to {
        -webkit-transform: rotate(360deg) translateZ(0);
    }
}

#wrapper {
    position: absolute;
    z-index: 1;
    top: 3rem;
    bottom: 0;
    width: 100%;
    background: #efeff4;
    overflow: auto;
    left: -9999px;
}

#scroller {
    position: absolute;
    background: #fff;
    z-index: 1;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    width: 100%;
    padding: 0;
    min-height: 45rem;
}

#scroller ul {
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
}

.iscontent {
    min-width: 7rem;
    max-width: 8rem;
    margin: 0 auto;
}

.isnodata {
    width: 50%;
    text-align: center;
    margin: 0 auto;
}

#star_pop {
    display: none;
    position: fixed;
    top: 50%;
    width: 100%;
    z-index: 99;
}

#star_pop div {
    width: 50%;
    margin: 0 auto;
    text-align: center;
    background: #585858;
    padding: 1rem 0;
    color: #fff;
    font-size: .9rem;
    border-radius: .5rem;
}
