.islider-outer,
.islider-outer>li {
    margin: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    padding: 0;
    list-style: none
}

.islider-outer {
    display: block;
    position: relative
}

.islider-outer>li {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    -moz-box-pack: center;
    -webkit-box-pack: center;
    box-pack: center;
    -moz-box-align: center;
    -webkit-box-align: center;
    box-align: center
}

.islider-img>img {
    max-width: 100%;
    max-height: 100%
}

.islider-sliding {
    z-index: 0
}

.islider-sliding-focus {
    z-index: 1
}

.islider-btn-outer {
    position: absolute;
    width: 3rem;
    height: 3rem;
    cursor: pointer;
    top: 0;
    bottom: 0;
    margin: auto 0;
    display: block;
    background-color: #777
}

.islider-btn-outer.left {
    left: 5%
}

.islider-btn-outer.right {
    right: 5%
}

.islider-btn-inner,
.islider-dot-wrap {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0
}

.islider-btn-inner {
    width: 50%;
    height: 50%;
    border-top: 2px solid #fff;
    border-left: 2px solid #fff;
    margin: auto;
    top: 0
}

.islider-btn-outer.left>.islider-btn-inner {
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    -o-transform: rotate(-45deg);
    transform: rotate(-45deg)
}

.islider-btn-outer.right>.islider-btn-inner {
    -webkit-transform: rotate(135deg);
    -moz-transform: rotate(135deg);
    -ms-transform: rotate(135deg);
    -o-transform: rotate(135deg);
    transform: rotate(135deg)
}

.islider-dot-wrap {
    width: 80%;
    height: 10%;
    margin: 0 auto;
    padding: 0;
    z-index: 10000;
    text-align: center
}

.islider-dot {
    position: relative;
    display: inline-block;
    width: .8rem;
    height: .8rem;
    border-radius: 50%;
    border: 1px solid #fff;
    list-style: none;
    margin: 5px
}

.islider-dot.active {
    background-color: #fff
}
